.catalog {
	padding-bottom : 64px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 96px;
	}
}

.catalog__wrapper {
	display               : grid;
	grid-gap              : 32px 12px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 32px 26px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 32px 24px;
	}
}

.product-card--catalog {
	grid-column    : 2 span;
	margin         : 0;
	padding-bottom : 40px;
	position       : relative;
	z-index        : 1;

	@media screen and (min-width : $break-table) {
		grid-column : 4 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}
}

.tabs--catalog {
	.tabs__head {

		cursor         : pointer;
		margin-bottom  : 32px;
		padding-bottom : 0;
		position       : relative;

		@media screen and (min-width : $break-table) {
			border : none;
		}
		@media screen and (min-width : $break-desktop) {

			margin-bottom : 96px;
			max-width     : none;
			padding       : 0;
		}

		.tabs__text {
			border        : 0.5px solid #CCC;
			border-radius : 12px;
		}

		.tabs__text .tabs__selected {
			border-bottom : none;
			position      : relative;

			&:before {
				background-color : #CCC;
				bottom           : 20px;
				content          : "";
				display          : none;
				height           : 1px;
				left             : 0;
				position         : absolute;
				width            : 100%;
				z-index          : 1;
			}
		}

		&.active {
			.tabs__buttons {
				border                     : 0.5px solid #CCC;
				border-bottom-left-radius  : 12px;
				border-bottom-right-radius : 12px;
				border-top                 : none;
				opacity                    : 1;
				transform                  : scaleY(1) translateX(-50%);
			}

			.tabs__text .tabs__selected {
				padding-bottom : 42px;

				&:before {
					display : block;
				}
			}
		}
	}

	.tabs__buttons {
		background-color           : #F6F6F6;
		border                     : 0.5px solid #CCC;
		border-bottom-left-radius  : 12px;
		border-bottom-right-radius : 12px;
		border-top                 : none;
		left                       : 50%;
		margin-top                 : -8px;
		max-height                 : 321px;
		opacity                    : 0;
		padding                    : 0 24px 24px;
		position                   : absolute;
		transform                  : scaleY(0) translateX(-50%);
		transform-origin           : 0 0;
		transition                 : all .3s ease;
		width                      : 100%;

		@media screen and (min-width : $break-table) {
			background-color           : #F6F6F6;
			border                     : 0.5px solid #CCC;
			border-bottom-left-radius  : 12px;
			border-bottom-right-radius : 12px;
			border-top                 : none;
			display                    : block;
			left                       : 50%;
			margin-top                 : -8px;
			max-height                 : 321px;
			opacity                    : 0;
			padding                    : 0 24px 24px;
			position                   : absolute;
			transform                  : scaleY(0) translateX(-50%);
			transform-origin           : 0 0;
			transition                 : all .3s ease;
			width                      : 100%;
		}

		@media screen and (min-width : $break-desktop) {
			background-color      : transparent;
			border                : none;
			box-shadow            : unset;
			display               : grid;
			grid-gap              : 24px;
			grid-template-columns : repeat(12, 1fr);
			left                  : unset;
			margin                : 0;
			opacity               : 1;
			padding               : 0;
			position              : unset;
			transform             : unset;
		}
	}


	.tabs__item {
		margin-left : 0;

		&:not(:last-child) {
			margin-bottom : 20px;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 3 span;

			&:not(:last-child) {
				margin-bottom : 0;
			}
		}

		&:nth-child(1) {
			.tabs__button {
				@media screen and (min-width : $break-desktop) {
					background-image : url("../images/home/destination/destination-1-desktop.jpg");
				}
			}
		}

		&:nth-child(2) {
			.tabs__button {
				@media screen and (min-width : $break-desktop) {
					background-image : url("../images/home/destination/destination-2-desktop.jpg");
				}
			}
		}

		&:nth-child(3) {
			.tabs__button {
				@media screen and (min-width : $break-desktop) {
					background-image : url("../images/home/destination/destination-3-desktop.jpg");
				}
			}
		}

		&:nth-child(4) {
			.tabs__button {
				@media screen and (min-width : $break-desktop) {
					background-image : url("../images/home/destination/destination-4-desktop.jpg");
				}
			}
		}
	}

	.tabs__button {

		@media screen and (min-width : $break-table) {
			background-color : transparent;
			border           : none;
			border-bottom    : 1px solid #CCC;
			color            : #333;
			font-size        : 14px;
			font-weight      : 400;
			line-height      : 20px;
			padding-bottom   : 20px;
			text-align       : start;
			width            : 100%;
		}

		@media screen and (min-width : $break-desktop) {
			background-color  : #F6F6F6;
			background-repeat : no-repeat;
			background-size   : cover;
			border-bottom     : none;
			display           : flex;
			flex-direction    : column;
			height            : 180px;
			opacity           : 0.6000000238418579;
			padding           : 24px 24px;
			transition        : margin-top .3s ease, opacity .3s ease;

			&:hover {
				margin-top : -10px;
				opacity    : 1;
			}
		}


		&.active {
			color   : $black;
			opacity : 1;
		}

		&::after {
			display : none;
		}
	}

	.tabs__text {
		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	.tabs__name {
		max-width : 140px;

		@media screen and (min-width : $break-table) {
			max-width : 200px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			font-weight : 700;
			line-height : 20px;
			margin-top  : auto;
		}
	}
}

.catalog-banner {
	background-color : $pink;
	border-radius    : 8px;
	display          : block;
	position         : relative;

	&:not(:last-child) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.catalog-banner__wrapper {
	padding : 24px 24px;

	@media screen and (min-width : $break-table) {
		padding : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.catalog-banner__title {
	color         : $white;
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		font-weight : 700;
		line-height : 32px;
	}
}

.catalog-banner__group {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column-reverse;

	@media screen and (min-width : $break-table) {
		align-items    : center;
		flex-direction : row;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.catalog-banner__text {
	color         : $white;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 24px;
	max-width     : 289px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		margin-left   : 12px;
		max-width     : 201px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 20px;
	}
}

.catalog-banner__image {
	bottom   : 0;
	height   : 97px;
	overflow : hidden;
	position : absolute;
	right    : -1px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom    : unset;
		height    : 155px;
		top       : 50%;
		transform : translateY(-50%);
	}

	@media screen and (min-width : $break-desktop) {
		height : 179px;
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}
