.production {
	padding-bottom : 32px;

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 48px;
	}
}

.production__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.production__title {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.production__main {
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.production__subtitle {
	color         : $dark-grey;
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 28px;
	}
}

.production__text {
	font-size   : 14px;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.production__img {
	border-radius : 8px;
	grid-column   : 4 span;
	grid-row      : 2/2;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		grid-column   : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
		grid-row    : unset;
	}


	img {
		border-radius : 8px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;

		@media screen and (min-width : $break-table) {
			border-radius : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.production__bottom {
	display               : grid;
	grid-column           : 4 span;
	grid-gap              : 48px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column           : 12 span;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 104px;
	}
}

.production__item {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}

	h4 {
		font-size     : 14px;
		font-weight   : 700;
		line-height   : 20px;
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 20px;
			max-width   : 184px;
		}
	}

	.production__text {
		font-size   : 14px;
		line-height : 20px;
	}
}

.production__icon {
	height        : 32px;
	margin-bottom : 12px;
	width         : 32px;

	@media screen and (min-width : $break-table) {
		height        : 40px;
		margin-bottom : 24px;
		width         : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.production__wrap {
	display    : flex;
	margin-top : 32px;

	@media screen and (min-width : $break-table) {
		display               : grid;
		grid-template-columns : repeat(12, 1fr);
		margin-top            : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		display    : flex;
		margin-top : auto;
	}

	.production__text {
		font-size   : 14px;
		line-height : 20px;


	}
}

.production__group {
	grid-column : 6 span;


	&:last-child {
		margin-left : 32px;

		@media screen and (min-width : $break-table) {
			margin-left : 22px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 48px;
		}
	}
}

.production__count {
	color         : $pink;
	font-size     : 24px;
	font-weight   : 700;
	line-height   : 30px;
	margin-bottom : 4px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 40px;
		line-height   : 46px;
		margin-bottom : 8px;
	}

}

