.certificates {
	padding : 32px 0 64px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0 96px 0;
	}
}

.certificates__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	.slider-nav {
		display : none;
		@media screen and (min-width : $break-table) {
			display : flex;
		}

	}
}

.swiper--certificates {
	margin : 0 -12px;

	.swiper-slide {
		display : flex;
		height  : auto;
		width   : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.swiper-pagination {
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.certificates-item {
	display : flex;
	margin  : 0 12px;
	width   : 100%;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.certificates-item__wrap {
	margin-left : 24px;

	@media screen and (min-width : $break-table) {
		margin-left : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.certificates-item__name {
	font-size     : 14px;
	font-weight   : 700;
	line-height   : 20px;
	margin-bottom : 24px;
	max-width     : 252px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 28px;
		max-width   : 237px;
	}
}
