.dealer-rebay {
	padding : 32px 0 32px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 57px 0 48px 0;
	}
}

.dealer-rebay__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 32px;
	}
}

.dealer-rebay__title {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 800px;
	}

}

.dealer-rebay__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
	}
}

.dealer-rebay__text {
	color         : $dark-grey;
	font-size     : 14px;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 48px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.dealer-rebay__img {
	border-radius : 8px;
	grid-column   : 4 span;
	grid-row      : 2/2;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		grid-column   : 6 span;
		grid-row      : unset;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
	}

	img {
		border-radius : 8px;
		object-fit    : cover;
		width         : 100%;
		@media screen and (min-width : $break-table) {
			border-radius : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}

	}
}

.dealer-rebay__right {
	background-color : $light-grey-2;
	border-radius    : 8px;
	grid-column      : 4 span;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
		padding     : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		border-radius : 12px;
		grid-column   : 4 span;
	}

	.dealer-rebay__text--bold {
		color         : $dark-grey;
		font-size     : 16px;
		font-weight   : 700;
		line-height   : 20px;
		margin-bottom : 24px;
	}

	.dealer-rebay__text {
		color       : $dark-grey;
		font-size   : 14px;
		line-height : 20px;
	}
}
