.leftovers {
	padding-bottom : 32px;

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 48px;
	}
}

.leftovers__head {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : 872px) {

	}

}

.leftovers__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}
}

.leftovers__table {
	margin     : 0 -14px;
	overflow-x : auto;
	padding    : 0 14px;
	@media screen and (min-width : 872px) {
		margin  : 0;
		padding : 0;
	}
}

.leftovers-table {
	background-color : $light-grey-2;
	border-radius    : 12px;

	max-width        : none;
	overflow-x       : auto;
	padding          : 24px 24px;
	width            : 1000px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : 872px) {
		max-width : 100%;
		padding   : 32px 32px;
		width     : 100%;
	}

	thead {

	}

	thead > tr > td {
		border-bottom : 1px solid rgba(204, 204, 204, 0.50);
		font-size     : 12px;
		line-height   : 18px;
		margin        : 0 -10px;
		padding       : 0 10px 16px 10px;

		@media screen and (min-width : $break-desktop) {
			font-size   : 14px;
			line-height : 20px;
			padding     : 0 10px 24px 10px;
		}
	}

	tbody > tr > td {
		border-bottom : 1px solid rgba(204, 204, 204, 0.50);
		font-size     : 12px;
		line-height   : 18px;
		padding       : 16px 10px 16px 10px;

		@media screen and (min-width : $break-desktop) {
			font-size   : 14px;
			line-height : 20px;
			padding     : 24px 10px 24px 10px;
		}
	}
}
