.benefit {
	padding : 32px 0 32px 0;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 57px 0 48px 0;
	}
}

.benefit__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.benefit__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(8, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.benefit__item {
	background-color : $light-grey-2;
	border-radius    : 8px;
	grid-column      : 4 span;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		grid-column   : 4 span;
		padding       : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}

	&--img {
		height  : 144px;
		padding : 0;

		@media screen and (min-width : $break-table) {
			height : 196px;
		}

		@media screen and (min-width : $break-desktop) {
			height : auto;
		}
	}
}

.benefit__icon {
	height        : 32px;
	margin-bottom : 16px;
	width         : 32px;

	@media screen and (min-width : $break-table) {
		height        : 40px;
		margin-bottom : 32px;
		width         : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.benefit__text {
	font-size   : 14px;
	line-height : 20px;
}

.benefit__img {
	height : 100%;

	img {
		border-radius : 8px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;

		@media screen and (min-width : $break-table) {
			border-radius : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}
