.hero-dealers {
	padding-bottom : 32px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 57px;
	}
}

.hero-dealers__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 0;
	}
}

.hero-dealers__title {
	grid-column : 1/5;


	@media screen and (min-width : $break-table) {
		grid-column : 1/12;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column   : 1/7;
		margin-bottom : 32px;
	}
}

.hero-dealers__list {
	grid-column : 1/5;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
		grid-row    : 2/2;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column   : 1/7;
		margin-bottom : 32px;
	}

	li {
		align-items : flex-start;
		color       : $dark-grey;
		display     : flex;
		font-size   : 14px;
		line-height : 20px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}

		&:before {
			background-image  : url("../images/icon/chevron-black.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			display           : inline-block;
			height            : 24px;
			width             : 24px;
		}

		&:not(:last-child) {
			margin-bottom : 8px;
		}
	}
}

.hero-dealers__wrap {
	align-items : center;
	display     : flex;
	grid-column : 1/5;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/7;
	}


	.b-btn--pink {
		padding : 15px 16px;
	}

	span {
		display     : inline-block;
		font-size   : 12px;
		line-height : 18px;
		margin-left : 24px;
		max-width   : 220px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 14px;
			line-height : 20px;
		}
	}
}

.hero-dealers__img {
	grid-column : 1/5;
	grid-row    : 2/2;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 2/4;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 7/13;
		grid-row    : 1/5;
	}

	img {
		border-radius : 12px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;
	}
}
