.dealers-list {
	padding-bottom : 96px;
}

.dealers-list__title {
	margin-bottom : 32px;
}

.dealers-list__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		gap             : 32px;
		justify-content : space-between;
	}
}

.dealers-list__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 312px;
	}
}

.dealers-list__wrap {
	display : none;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-color : $light-grey-2;
		border-radius    : 12px;
		min-height       : 533px;
		padding          : 24px 0 24px 24px;
	}

	&.active {
		display : block;
	}
}

.dealers-list__list {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		display       : block;
		max-height    : 485px;
		overflow-y    : auto;
		padding-right : 24px;
	}

	&::-webkit-scrollbar {
		width : 2px;
	}

	&::-webkit-scrollbar-track {
		background    : bisque;
		border-radius : 2px;
		box-shadow    : inset 0 0 5px grey;
		opacity       : 0;
		visibility    : hidden;
	}

	&::-webkit-scrollbar-thumb {
		background    : $grey;
		border-radius : 2px;
	}
}

.dealers-list__item {
	background-color : $light-grey-2;
	border-radius    : 12px;
	grid-column      : 4 span;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		background-color : transparent;
		border-bottom    : 1px solid $grey-2;
		border-radius    : 0;
		padding          : 0 0 16px 0;

		&:not(:last-child) {
			margin-bottom : 16px;
		}
	}


}

.dealers-item {
	&__top {
		margin-bottom : 8px;
	}

	&__name {
		color       : $dark-grey;
		font-size   : 14px;
		font-weight : 700;
		line-height : 20px;
	}

	&__site {
		color       : $pink;
		font-size   : 12px;
		line-height : 18px;
	}

	&__mid {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		margin-bottom  : 8px;
	}

	&__address {
		color       : $grey;
		font-size   : 12px;
		line-height : 18px;
	}

	&__link {
		color       : $grey;
		display     : inline-block;
		font-size   : 12px;
		line-height : 18px;
	}

	&__service {
		color       : $grey;
		font-size   : 12px;
		line-height : 18px;
	}
}

.dealers-list__right {
	display : none;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : block;
	}
}

.dealers-list__wrapper {
	//opacity    : 1;
	//overflow   : visible;
	//transition : opacity .3s ease, overflow .3s ease;
	//
	//&.hidden {
	//	opacity  : 0;
	//	overflow : hidden;
	//}
}

.map {
	&__item {
		cursor     : pointer;
		fill       : #CCC;
		transition : fill .3s ease;

		&:hover {
			fill : $light-pink;
		}

		&.active {
			fill : $pink;
		}

		&:focus {
			outline : -webkit-focus-ring-color auto 0;
		}
	}
}

.map__item {

}

.dealers-dropdown {


	&__head {

		border-radius : 12px;
		cursor        : pointer;
		height        : 70px;
		margin-bottom : 20px;
		position      : relative;
		z-index       : 10;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			border : none;
		}

		&.active {
			.dealers-dropdown__list {
				border                     : 0.5px solid #CCC;
				border-bottom-left-radius  : 12px;
				border-bottom-right-radius : 12px;
				border-top                 : none;
				opacity                    : 1;
				transform                  : scaleY(1) translateX(-50%);

				@media screen and (min-width : $break-table) {

				}

				@media screen and (min-width : $break-desktop) {
					border : none;
				}
			}

			.dealers-dropdown__text {
				border-bottom-left-radius  : 0;
				border-bottom-right-radius : 0;
				padding-bottom             : 0;

				&:before {
					transform : rotate(180deg);
				}

				.dealers-dropdown__selected {
					border-bottom  : 1px solid #CCC;
					padding-bottom : 24px;

					&:before {
						background-color : #CCC;
						bottom           : 1px;
						content          : "";
						height           : 1px;
						left             : 0;
						position         : absolute;
						width            : 100%;
						z-index          : 1;
					}

					@media screen and (min-width : $break-table) {

					}

					@media screen and (min-width : $break-desktop) {
						&:before {
							display : none;
						}
					}
				}
			}
		}
	}

	&__text {
		background    : #F6F6F6;
		border        : 0.5px solid #CCC;
		border-radius : 12px;
		color         : #333;
		font-size     : 14px;
		font-weight   : 700;
		line-height   : 20px;
		padding       : 24px;
		position      : relative;
		width         : 100%;

		@media screen and (min-width : $break-desktop) {
			border : none;
		}

		&:before {
			background-image  : url('../images/icon/chevron-down.svg');
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			height            : 15px;
			position          : absolute;
			right             : 27px;
			top               : 27px;
			transform         : rotate(0deg);
			transition        : transform .3s ease;
			width             : 16px;
			z-index           : 1;
		}
	}

	&__selected {
		display : flex;
		width   : 100%;
	}

	&__list {
		background-color           : #F6F6F6;
		border-bottom-left-radius  : 12px;
		border-bottom-right-radius : 12px;
		left                       : 50%;
		margin-top                 : -3px;
		max-height                 : 321px;
		opacity                    : 0;
		overflow-y                 : auto;
		padding                    : 0 24px 24px 24px;
		position                   : absolute;
		transform                  : scaleY(0) translateX(-50%);
		transform-origin           : 0 0;
		transition                 : all .3s ease;
		width                      : 100%;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-top : 0;
			max-height : 551px;
			width      : 100%;
		}

		&::-webkit-scrollbar {
			width : 2px;
		}

		&::-webkit-scrollbar-track {
			background    : bisque;
			border-radius : 2px;
			box-shadow    : inset 0 0 5px grey;
			opacity       : 0;
			visibility    : hidden;
		}

		&::-webkit-scrollbar-thumb {
			background    : $grey;
			border-radius : 2px;
		}
	}

	&__item {

	}

	&__btn {
		background-color : transparent;
		border           : 0;
		border-bottom    : 1px solid #CCC;
		color            : #333;
		font-size        : 14px;
		font-weight      : 400;
		line-height      : 20px;
		padding          : 20px 0;
		text-align       : start;
		width            : 100%;


	}

	&__selected {
		position : relative;
	}
}
