.news {
	padding-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 48px;
	}
}

.news__wrapper {
	margin    : 0 auto;
	max-width : 808px;
}

.news__main {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		align-items   : flex-start;
		display       : flex;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}
}

.news__img {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 392px;
	}

	img {
		border-radius : 12px;
		height        : 100%;
		width         : 100%;
	}
}

.news__wrap {


	@media screen and (min-width : $break-table) {
		margin-left : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.news__date {
	align-items   : center;
	color         : $grey;
	display       : flex;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 16px;

	&:before {
		background-image  : url("../images/icon/calendar.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : inline-block;
		height            : 20px;
		margin-right      : 6px;
		width             : 20px;
	}

	@media screen and (min-width : $break-table) {
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 12px;
	}
}

.news__title {
	color         : $black;
	font-size     : 22px;
	font-weight   : 700;
	line-height   : 30px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 28px;
	}
}

.news__about {
	color       : $dark-grey;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.news__group {
	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 32px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 48px;
		}
	}
}

.news__text {
	color       : $dark-grey;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 24px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.news__poster {
	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 32px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 48px;
		}
	}

	img {
		border-radius : 12px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;
	}
}
