.rebay {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0;
	}
}

.rebay__wrapper {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		align-items : flex-start;
	}
}

.reaby__title {
	grid-column : 1/5;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/13;
		max-width   : 963px;
	}
}

.rebay__about {
	grid-column : 1/5;

	@media screen and (min-width : $break-table) {
		grid-column : 1/9;
		grid-row    : 2/4;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/5;
		grid-row    : 2/4;
	}
}

.rebay__text {
	color         : $dark-grey;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 48px;
	}
}

.rebay__link {

}

.link {
	align-items : center;
	color       : $dark-grey;
	display     : inline-flex;
	font-size   : 14px;
	font-weight : 700;
	line-height : 20px;
	transition: color .3s ease;

	&:hover {
		color: $pink;
	}

	//&:after {
	//	background-image  : url("../images/icon/chevron.svg");
	//	background-repeat : no-repeat;
	//	background-size   : cover;
	//	content           : "";
	//	display           : inline-block;
	//	height            : 24px;
	//	margin-left       : 4px;
	//	width             : 24px;
	//}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 20px;
	}
}

.rebay__img {

	&--small {
		grid-column : 1/5;
		grid-row    : 4/4;

		@media screen and (min-width : $break-table) {
			grid-column : 9/13;
			grid-row    : 5/8;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 5/9;
			grid-row    : 2/4;
		}
	}

	&--big {
		grid-column : 1/5;
		grid-row    : 2/2;

		@media screen and (min-width : $break-table) {
			grid-column : 9/13;
			grid-row    : 1/5;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 9/13;
			grid-row    : 2/7;
		}
	}

	img {
		border-radius : 12px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;
	}
}

.rebay__wrap {
	display         : flex;
	flex-direction  : column;
	grid-column     : 1/5;
	grid-row        : 5/5;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {
		background-color : $light-grey-2;
		border-radius    : 12px;
		grid-column      : 1/9;
		grid-row         : 4/8;
		padding          : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/9;
		grid-row    : 4/7;
		padding     : 32px 32px;
	}
}

.rebay__icon {
	display : none;

	@media screen and (min-width : $break-table) {
		display       : block;
		margin-bottom : 62px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 48px;
	}

	ul {
		display : flex;

		li {
			&:not(:first-child) {
				margin-left : -25px;
			}
		}
	}
}

.rebay__counts {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		justify-content : flex-end;
	}
}

.rebay__count {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-right : 20px;
	}

	&:not(:first-child) {


		@media screen and (min-width : $break-table) {
			margin-left : 71px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 82px;
		}
	}

	div {
		&:first-child {
			color         : $pink;
			font-size     : 24px;
			font-weight   : 700;
			line-height   : 30px;
			margin-bottom : 4px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : 900px) {
				font-size     : 40px;
				line-height   : 46px;
				margin-bottom : 16px;
			}
		}

		&:last-child {
			color       : $dark-grey;
			font-size   : 14px;
			font-weight : 400;
			line-height : 20px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : 900px) {
				font-size   : 16px;
				line-height : 20px;
			}
		}
	}
}

.arrow {
	--arrow-size: 16px;
	--arrow-color: #E61F4D;
	--arrow-color-hover: #CCCCCC;
	position: relative;
	display: inline-flex;
	vertical-align: middle;
	width: var(--arrow-size);
	height: var(--arrow-size);
	overflow: hidden;
	margin-left: 5px;

	&::after {
		content: '';
		position: absolute;
		top: 50%;
		box-sizing: border-box;
		display: inline-block;
		width: calc( var(--arrow-size) * .7071);
		height: calc( var(--arrow-size) * .7071);
		border-top: calc( var(--arrow-size) / 5 ) solid var(--arrow-color);
		transition: all 150ms ease-in-out;
	}


	&.right {
		&::after {
			right: calc( var(--arrow-size) / 5 );
			border-right: calc( var(--arrow-size) / 5 ) solid var(--arrow-color);
			transform-origin: top right;
			rotate: 45deg;
		}

		&:hover::after {
			box-shadow: calc( var(--arrow-size) / 8 ) calc( var(--arrow-size) / -8 ) 0 var(--arrow-color);
		}
	}
}

a {
	&:hover .arrow {
		&.left,
		&.right {
			&::after {
				border-color: var(--arrow-color-hover);
			}
		}


		&.right {
			&::after {
				box-shadow: calc( var(--arrow-size) / 8 ) calc( var(--arrow-size) / -8 ) 0 var(--arrow-color);
			}
		}
	}
}
