.contacts {
	padding-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 48px;
	}
}

.contact--page {
	.contact__wrapper {
		grid-gap              : 16px;
		grid-template-columns : repeat(4, 1fr);

		@media screen and (min-width : $break-table) {
			grid-gap              : 24px;
			grid-template-columns : repeat(12, 1fr);
		}

		@media screen and (min-width : $break-desktop) {

		}

		&--office {
			.contact__item {
				&:nth-of-type(1) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/7;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 1/4;
					}
				}

				&:nth-of-type(2) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 7/13;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 4/7;
					}
				}

				&:nth-of-type(3) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/7;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 1/4;
					}
				}

				&:nth-of-type(4) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 7/13;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 4/7;
					}
				}

				&:nth-of-type(5) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/13;
					}

					@media screen and (min-width : $break-desktop) {

					}
				}
			}

			.contact__text {

				&:last-child {
					@media screen and (min-width : $break-desktop) {
						left     : -27px;
						position : relative;
					}

				}
			}
		}

		&--composition {
			.contact__item {
				&:nth-of-type(1) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/13;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 1/7;
					}
				}

				&:nth-of-type(2) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 7/13;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 4/7;
					}
				}

				&:nth-of-type(3) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/7;
						grid-row    : 3/3;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 1/4;
						grid-row    : 2/2;
					}
				}

				&:nth-of-type(4) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/13;
					}

					@media screen and (min-width : $break-desktop) {

					}
				}
			}

			.contact__info {
				max-width : none;
			}

			.contact__group {
				background-color      : $light-grey-2;
				border-radius         : 8px;
				display               : grid;
				grid-column           : 4 span;
				grid-gap              : 24px;
				grid-template-columns : repeat(4, 1fr);
				padding               : 24px 24px;

				@media screen and (min-width : $break-table) {
					border-radius         : 12px;
					grid-column           : 1/13;
					grid-gap              : 58px;
					grid-template-columns : repeat(12, 1fr);
					padding               : 32px 32px;
				}

				@media screen and (min-width : $break-desktop) {

				}

				.contact__item {
					background-color : transparent;
					grid-column      : 6 span;
					padding          : 0;
				}
			}
		}

		&--center {
			.contact__item {
				&:nth-of-type(1) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/7;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 1/4;
					}
				}

				&:nth-of-type(2) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 7/13;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 4/7;
					}
				}

				&:nth-of-type(3) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/7;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 1/4;
					}
				}

				&:nth-of-type(4) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 7/13;
					}

					@media screen and (min-width : $break-desktop) {
						grid-column : 4/7;
					}
				}

				&:nth-of-type(5) {
					grid-column : 4 span;

					@media screen and (min-width : $break-table) {
						grid-column : 1/13;
					}

					@media screen and (min-width : $break-desktop) {

					}
				}
			}
		}
	}

	.contact__item {
		background-color : $light-grey-2;
		border-radius    : 8px;
		padding          : 24px 24px;

		@media screen and (min-width : $break-table) {
			border-radius : 12px;
			padding       : 32px 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.contact__map {
		grid-column : 4 span;
		grid-row    : 1/1;

		@media screen and (min-width : $break-table) {
			grid-column : 1/13;
			grid-row    : 1/1;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 7/13;
			grid-row    : 1/3;
		}
	}

	.contact__about {
		display        : flex;
		flex-direction : column;
		gap            : 12px;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			align-items     : center;
			flex-direction  : row;
			gap             : 0;
			justify-content : space-between;
		}

		.contact__text {

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				max-width : 520px;
			}
		}
	}

	.contact__text {
		color       : $dark-grey;
		font-size   : 12px;
		font-weight : 400;
		line-height : 18px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.tabs__head {
	height        : 70px;
	margin-bottom : 20px;
	position      : relative;
	z-index       : 10;

	@media screen and (min-width : $break-table) {
		border-bottom  : 1px solid $grey-2;
		height         : auto;
		margin-bottom  : 24px;
		padding-bottom : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom  : 32px;
		max-width      : 600px;
		padding-bottom : 24px;
	}

	&.active {
		.tabs__text {
			border-bottom-left-radius  : 0;
			border-bottom-right-radius : 0;
			padding-bottom             : 0;

			&:before {
				transform : rotate(180deg);
			}

			.tabs__selected {
				border-bottom  : 1px solid #CCC;
				padding-bottom : 42px;

			}
		}

		.tabs__buttons {
			border                     : 0.5px solid #CCC;
			border-bottom-left-radius  : 12px;
			border-bottom-right-radius : 12px;
			border-top                 : none;
			opacity                    : 1;
			transform                  : scaleY(1) translateX(-50%);
		}

		.tabs__selected {
			&:after {
				display : block;
			}
		}
	}
}

.tabs__buttons {
	background-color           : #F6F6F6;
	border                     : 0.5px solid #CCC;
	border-bottom-left-radius  : 12px;
	border-bottom-right-radius : 12px;
	border-top                 : none;
	left                       : 50%;
	margin-top                 : -8px;
	max-height                 : 321px;
	opacity                    : 0;
	padding                    : 0 24px 24px;
	position                   : absolute;
	transform                  : scaleY(0) translateX(-50%);
	transform-origin           : 0 0;
	transition                 : all .3s ease;
	width                      : 100%;

	@media screen and (min-width : $break-table) {
		background-color : transparent;
		border           : none;
		box-shadow       : unset;
		display          : flex;
		margin           : 0;
		opacity          : 1;
		padding          : 0;
		position         : unset;
		transform        : unset;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.tabs__item {
	&:not(:last-child) {
		margin-bottom : 20px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 0;

		}
	}

	@media screen and (min-width : $break-table) {
		& + & {
			margin-left : 48px;
		}

	}

}

.tabs__selected {
	display  : flex;
	position : relative;
	width    : 100%;

	&:after {
		background-color : #CCC;
		bottom           : 20px;
		content          : "";
		display          : none;
		height           : 1px;
		left             : 0;
		position         : absolute;
		width            : 100%;
		z-index          : 1;
	}
}

.tabs__text {
	background    : #F6F6F6;
	border        : 0.5px solid #CCC;
	border-radius : 12px;
	color         : #333;
	font-size     : 14px;
	font-weight   : 700;
	line-height   : 20px;
	padding       : 24px 24px;
	position      : relative;
	width         : 100%;

	&:before {
		background-image  : url("../images/icon/chevron-down.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 15px;
		position          : absolute;
		right             : 27px;
		top               : 27px;
		transform         : rotate(0deg);
		transition        : transform .3s ease;
		width             : 16px;
		z-index           : 1;
	}

	@media screen and (min-width : $break-table) {
		display : none;
	}

}

.tabs__button {
	background-color : transparent;
	border           : none;
	border-bottom    : 1px solid #CCC;
	color            : #333;
	font-size        : 14px;
	font-weight      : 400;
	line-height      : 20px;
	padding-bottom   : 20px;
	text-align       : start;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		background-color : transparent;
		border           : none;
		color            : #999;
		font-size        : 16px;
		font-weight      : 400;
		line-height      : 24px;
		padding          : 0;
		position         : relative;
		transition       : color .3s ease;

		&:after {
			background-color : $pink;
			bottom           : -18px;
			content          : "";
			height           : 2px;
			left             : 0;
			position         : absolute;
			transition       : width .3s ease;
			width            : 0;
			z-index          : 1;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				bottom : -26px;
			}
		}
	}


}

.tabs__button:hover {

	@media screen and (min-width : $break-table) {
		color : $dark-grey;
	}

}

.tabs__button.active {

	@media screen and (min-width : $break-table) {
		color : $pink;

		&:after {
			width : 100%;
		}

	}

}

.tabs__content {
	display : none;
}

.tabs__content.active {
	display : block;
}


.select-menu {
	margin : 150px 0;
	width  : 380px;
}

.select-menu {
	&.active .options {
		display : block;
	}

	.select-btn {
		align-items      : center;
		background-color : #FFF;
		border-radius    : 8px;
		box-shadow       : 0 0 5px rgba(0, 0, 0, 0.1);
		cursor           : pointer;
		display          : flex;
		font-size        : 18px;
		font-weight      : 400;
		height           : 55px;
		justify-content  : space-between;
		padding          : 20px;
	}

	.options {
		background-color : #FFF;
		border-radius    : 8px;
		box-shadow       : 0 0 3px rgba(0, 0, 0, 0.1);
		display          : none;
		margin-top       : 10px;
		padding          : 20px;
		position         : relative;

		.option {
			align-items      : center;
			background-color : #FFF;
			border-radius    : 8px;
			cursor           : pointer;
			height           : 55px;
			padding          : 0 16px;

			&:hover {
				background-color : #F2F2F2;
			}
		}
	}
}
