.news-list {
	padding-bottom : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 48px;
	}
}

.news-list__title {
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 220px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 392px;
	}
}

.news-list__head {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		align-items     : start;
		display         : flex;
		justify-content : space-between;
		margin-bottom   : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items   : center;
		margin-bottom : 64px;
	}
}

.news-list__text {


	@media screen and (min-width : $break-table) {
		max-width : 530px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 600px;
	}

}

.news-list__grid {
	display               : grid;
	grid-gap              : 32px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 32px 26px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 64px 24px;
	}

	.news-card {
		grid-column : 4 span;
		margin      : 0;
	}
}

.pagination {
	display         : flex;
	justify-content : center;
	margin-top      : 48px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 64px;
	}

	&__list {
		align-items : center;
		display     : flex;
	}

	&__item {
		&:first-child {
			margin-right : 24px;

		}

		&:nth-child(n+3) {
			margin-left : 8px;

			@media screen and (min-width : $break-table) {
				margin-left : 8px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}

		&:last-child {
			margin-left : 24px;

		}
	}

	&__link {
		align-items     : center;
		border          : 1px solid #CCC;
		border-radius   : 4px;
		color           : $grey;
		display         : flex;
		font-size       : 12px;
		font-weight     : 400;
		height          : 34px;
		justify-content : center;
		line-height     : 18px;
		transition      : color .3s ease, border .3s ease;
		width           : 34px;

		@media screen and (min-width : $break-desktop) {
			height : 40px;
			width  : 40px;
		}

		&:hover {
			border : 1px solid $light-pink;
			color  : $pink;

			svg {
				path {
					stroke     : $pink;
					transition : stroke .3s ease;
				}
			}
		}

		&.active {
			background-color : $light-grey;
			border-color     : transparent;
			color            : $grey;
		}

		svg {

			path {
				stroke     : #999;
				transition : stroke .3s ease;
			}

		}
	}
}

