.requisites {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0;
	}
}

.requisites__wrap {
	padding-bottom : 75px;
	position       : relative;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
	}
}

.requisites__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	.b-btn--grey {
		bottom    : 0;
		left      : 50%;
		position  : absolute;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			left      : unset;
			position  : unset;
			transform : unset;
		}
	}
}

.requisites__wrapper {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);


	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.requisites-item {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&__head {
		align-items   : center;
		display       : flex;
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 32px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&__title {
		color       : $dark-grey;
		font-size   : 18px;
		font-weight : 700;
		line-height : 24px;
		margin-left : 16px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
			margin-left : 24px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&__list {
		li {
			align-items   : start;
			border-bottom : 1px solid rgba(204, 204, 204, 0.5);
			padding       : 16px 0;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				display               : grid;
				grid-template-columns : repeat(8, 1fr);
				padding               : 24px 0;
			}

			&:first-child {
				border-top : 1px solid rgba(204, 204, 204, 0.5);
			}

			div {
				&:first-child {
					color         : $grey;
					font-size     : 12px;
					font-weight   : 400;
					line-height   : 18px;
					margin-bottom : 4px;

					@media screen and (min-width : $break-table) {
						grid-column   : 1/4;
						margin-bottom : 0;
					}

					@media screen and (min-width : $break-desktop) {

					}
				}

				&:last-child {
					color       : $dark-grey;
					font-size   : 14px;
					font-weight : 700;
					line-height : 20px;

					@media screen and (min-width : $break-table) {
						grid-column : 4/8;
					}

					@media screen and (min-width : $break-desktop) {

					}
				}
			}
		}
	}
}

.requisites__bottom {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		align-items : flex-start;
		display     : flex;
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.requisites__icon {
	height        : 32px;
	margin-bottom : 12px;
	width         : 32px;

	@media screen and (min-width : $break-table) {
		height        : 40px;
		margin-bottom : 0;
		width         : 40px;
	}
}

.requisites__group {


	@media screen and (min-width : $break-table) {
		margin-left : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	h4 {
		font-size   : 20px;
		font-weight : 700;
		line-height : 28px;
	}

	span {
		font-size   : 14px;
		line-height : 20px;
	}
}
