.dealer-about {
	padding : 32px 0 32px 0;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 57px 0 48px 0;
	}
}

.dealer-about__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		align-items           : flex-start;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.dealer-about__title {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 1/1;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column   : 1/7;
		margin-bottom : 24px;
	}
}

.dealer-about__text {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 2/2;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 7/13;
		grid-row    : 1/1;
	}
}

.dealer-about__img {
	border-radius : 8px;
	grid-column   : 4 span;
	grid-row      : 3/3;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		grid-column   : 7/13;
		grid-row      : 1/4;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 7/13;
		grid-row    : 2/6;
	}

	img {
		border-radius : 8px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;

		@media screen and (min-width : $break-table) {
			border-radius : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.dealer-about__item {
	background-color : $light-grey-2;
	border-radius    : 8px;
	display          : flex;
	flex-direction   : column;
	height           : 100%;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		padding       : 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--one {
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
			grid-row    : 4/4;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/4;
			grid-row    : 2/4;
		}
	}

	&--two {
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
			grid-row    : 4/4;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 4/7;
			grid-row    : 2/4;
		}
	}

	&--three {
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
			grid-row    : 5/5;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/4;
			grid-row    : 4/6;
		}
	}

	&--four {
		background-color : $light-pink-2;
		grid-column      : 4 span;

		@media screen and (min-width : $break-table) {
			grid-column : 6 span;
			grid-row    : 5/5;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 4/7;
			grid-row    : 4/6;
		}

		.dealer-about__descr {
			font-size   : 14px;
			font-weight : 700;
			line-height : 20px;

			@media screen and (min-width : $break-table) {
				font-size : 16px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}

.dealer-about__icon {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.dealer-about__descr {
	color       : $dark-grey;
	font-size   : 14px;
	line-height : 20px;
	margin-top  : auto;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.dealer-banner {
	background-color      : $light-grey-2;
	display               : grid;
	grid-column           : 4 span;
	grid-gap              : 32px;
	grid-template-columns : repeat(4, 1fr);
	padding               : 16px 24px 24px 24px;

	@media screen and (min-width : $break-table) {
		grid-column           : 1/13;
		grid-template-columns : repeat(12, 1fr);
		padding               : 44px 44px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.dealer-banner__left {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	grid-column    : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 20px 20px;
	}

	.b-btn--pink {
		margin-top : auto;
	}
}

.dealer-banner__text {
	font-size     : 22px;
	font-weight   : 700;
	line-height   : 30px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 40px;
		line-height   : 46px;
		margin-bottom : 24px;
	}
}

.dealer-banner__map {
	grid-column : 4 span;
	grid-row    : 1/1;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}

.dealer-banner__about {
	color         : $dark-grey;
	font-size     : 14px;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 0;
	}
}
