.product {
	padding : 0 0 32px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 0 0 48px 0;

	}
}

.product__wrapper {

	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 32px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 50px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items : flex-start;

	}
}

.product__zoom {
	background-color : transparent;
	border           : none;
	display          : none;
	padding          : 0;
	position         : absolute;
	right            : 12px;
	top              : 12px;
	z-index          : 1;

	@media screen and (min-width : $break-table) {
		display : block;
	}

	@media screen and (min-width : $break-desktop) {
		right : 16px;
		top   : 16px;
	}
}

.product__tooltip {
	bottom   : 12px;
	left     : 12px;
	position : absolute;

	@media screen and (min-width : $break-table) {
		bottom : unset;
		left   : 12px;
		top    : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		left : 16px;
		top  : 16px;
	}
}

.tooltip {

	height   : 40px;
	position : relative;
	width    : 40px;
}

.tooltip__btn {
	align-items     : center;
	background      : transparent;
	border          : none;
	display         : inline-flex;
	height          : 40px;
	justify-content : center;
	padding         : 0;
	width           : 40px;
}

.tooltip__txt {
	background-color : $pink;
	border-radius    : 4px;
	color            : #FFF;
	font-size        : 14px;
	font-weight      : 400;
	left             : 0px;
	line-height      : 20px;
	max-width        : 161px;
	opacity          : 0;
	padding          : 10px 10px;
	position         : absolute;
	text-transform   : none;
	top              : -80px;
	transform        : translateY(10px);
	transition       : opacity .3s ease-in-out, transform .3s ease-in-out, visibility .3s ease-in-out;
	visibility       : hidden;
	width            : max-content;
	z-index          : 3;

	&:after {
		border     : 10px solid transparent;
		border-top : 10px solid $pink;
		bottom     : -20px;
		content    : "";
		display    : inline-block;
		left       : 10px;
		position   : absolute;
	}

}

.tooltip__btn:hover + .tooltip__txt {
	opacity    : 1;
	transform  : translateY(0);
	visibility : visible;
}

.tooltip__btn:focus + .tooltip__txt {
	opacity    : 1;
	transform  : translateY(0);
	visibility : visible;
}

.product__main {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column    : 6 span;
		grid-row       : 1/5;
		padding-bottom : 8px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column    : 5 span;
		grid-row       : 1/5;
		padding-bottom : 0;
	}

	.swiper-pagination {
		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.product__slider {
	background-color : $light-grey-2;
	border-radius    : 12px;
	margin-bottom    : 12px;
	padding          : 80px 24px;
	position         : relative;
	text-align       : center;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 89px 36px;
	}
}

.product__top {
	border-bottom  : 1px solid #CCC;
	grid-column    : 4 span;
	padding-bottom : 24px;

	@media screen and (min-width : $break-table) {
		border-bottom  : none;
		grid-column    : 6 span;
		grid-row       : 1/2;
		padding-bottom : 8px;
	}

	@media screen and (min-width : $break-desktop) {
		border-bottom  : 1px solid #CCC;
		grid-column    : 4 span;
		padding-bottom : 32px;
	}
}

.product__name {
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 28px;
	}
}

.product__text {
	font-size   : 14px;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.product__bottom {
	grid-column : 4 span;


	@media screen and (min-width : $break-table) {
		border-top  : 1px solid #CCC;
		grid-column : 12 span;
		grid-row    : 5/5;
		padding-top : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		border-top  : none;
		grid-column : 4 span;
		grid-row    : 2/2;
		padding-top : 8px;

	}
}

.product__subtext {
	font-size     : 14px;
	line-height   : 20px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
	}
}

.product__list {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}

	li {
		align-items : flex-start;
		display     : flex;
		font-size   : 14px;
		line-height : 20px;

		&:not(:last-child) {
			margin-bottom : 8px;
		}

		&:before {
			background-image  : url("../images/icon/chevron-black.svg");
			background-repeat : no-repeat;
			background-size   : cover;
			content           : "";
			display           : inline-block;
			flex-shrink       : 0;
			height            : 20px;
			width             : 24px;
		}
	}
}

.product__doc {
	align-items : center;
	color       : $pink;
	display     : flex;
	font-size   : 14px;
	font-weight : 700;
	gap         : 8px;
	line-height : 20px;

	&:before {
		background-image  : url("../images/icon/pdf.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : inline-block;
		flex-shrink       : 0;
		height            : 22px;
		width             : 22px;
	}
}

.product__info {
	background-color : $light-grey-2;
	border-radius    : 12px;
	grid-column      : 4 span;
	padding          : 24px 24px;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		grid-row    : 2/4;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
		grid-row    : 1/3;
	}
}

.product__price {
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 28px;
		margin-bottom : 24px;
	}

}

.product__wrap {
	align-items   : center;
	display       : flex;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 16px;
	}
}

.product__status {
	align-items    : center;
	display        : flex;
	font-size      : 11px;
	letter-spacing : 0.33px;
	line-height    : 18px;
	text-transform : uppercase;

	&:before {
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : inline-block;
		height            : 15px;
		margin-right      : 8px;
		width             : 15px;
	}

	&--yes {
		color : $green;

		&:before {
			background-image : url("../images/icon/yes.svg");
		}
	}

	&--no {
		color : $pink;

		&:before {
			background-image : url("../images/icon/no.svg");
		}
	}
}

.product__article {
	color          : $grey;
	font-size      : 11px;
	letter-spacing : 0.33px;
	line-height    : 18px;
	margin-left    : 24px;
}

.product__btn {
	font-size     : 16px;
	font-weight   : 700;
	line-height   : 20px;
	margin-bottom : 8px;
	padding       : 16px 51px;
	width         : 100%;
}

.product__guarantee {
	font-size      : 11px;
	letter-spacing : 0.33px;
	line-height    : 18px;
}

.swiper--nav {
	display : none;
	@media screen and (min-width : $break-table) {
		display : block;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.swiper-slide {
		align-items      : center;
		background-color : $light-grey-2;
		border-radius    : 10.2px;
		display          : flex;
		height           : auto;
		justify-content  : center;
		margin           : 0 6px;
		opacity          : 0.5;
		padding          : 13.5px 7px;
		width            : 68px;

		img {
			height     : 100%;
			object-fit : cover;
			width      : 100%;
		}
	}

	.swiper-slide-thumb-active {
		opacity : 1;
	}
}

.swiper--product {
	.swiper-slide {
		a {
			pointer-events : none;
		}
	}
}

.glightbox-clean .gslide-media {

	box-shadow : 0 0 0 0 rgba(0, 0, 0, 0);
}


.accordion-tabs {

}

.accordion-tabs__list {
	display    : none;
	list-style : none;
	margin     : 0;
	padding    : 0 0 20px 0;

	@media screen and (min-width : $break-table) {
		justify-content : space-between;
		min-width       : 1300px;
	}

	@media screen and (min-width : $break-desktop) {
		justify-content : flex-start;
		min-width       : auto;
	}

	li {
		margin : 0;

		&:not(:first-child) {
			margin-left : 48px;
		}
	}

	@media screen and (min-width : $break-table) {
		display : flex;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.accordion-tabs__trigger {
	color       : #999;
	font-size   : 16px;
	line-height : 24px;
	position    : relative;
	transition  : color .3s ease;

	& + & {
		margin-left : 24px;
	}

	&:hover {
		border-bottom : none;
		color         : #000;
	}

	&:before {
		background-color : $pink;
		bottom           : -24px;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : width .3s ease;
		width            : 0;
	}

	&.is-selected,
	&.is-selected:hover,
	&.is-selected:focus {
		color : $pink;

		&:before {
			width : 100%;
		}
	}

}

.accordion-tabs__panel {
	display  : block;
	margin   : 0;
	padding  : 0;
	position : relative;

	&:not(:last-child) {
		margin-bottom : 16px;
	}

	@media screen and (min-width : $break-table) {
		&:not(:last-child) {
			margin-bottom : 0;
		}
	}

	@media screen and (min-width : $break-desktop) {
	}


	&.is-open {
		.accordion-tabs__btn {
			svg {
				transform  : rotate(180deg);
				transition : stroke .3s ease, transform .3s ease;

				path {
					stroke : $pink;
				}
			}
		}
	}

	.accordion-tabs__content {

		padding : 0;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-top : 32px;
		}
	}
}


.accordion-tabs__panel:active, .accordion-tabs__panel:focus {
	outline : none;
}

.is-initialized.tabs-allowed .accordion-tabs__panel {
	display : inherit;
}

.accordion-tabs__btn {
	align-items      : center;
	background-color : #F6F6F6;
	border-radius    : 12px;
	cursor           : pointer;
	display          : flex;
	font-size        : 14px;
	font-weight      : 700;
	justify-content  : space-between;
	line-height      : 20px;
	padding          : 24px 24px;

	svg {
		height         : 20px;
		pointer-events : none;
		transform      : rotate(0deg);
		transition     : stroke .3s ease, transform .3s ease;
		width          : 29px;

		path {
			stroke : #333;
		}
	}

	span {
		pointer-events : none;
	}
}

@media (min-width : 744px) {
	.tabs-allowed .accordion-tabs__btn {
		display : none;
	}
	.tabs-allowed .tabs-trigger {
		display : block;
	}
	.tabs-allowed .accordion-tabs__panel {
		display : none;
	}
	.tabs-allowed .accordion-tabs__panel.is-hidden {
		display : none;
	}
	.tabs-allowed .accordion-tabs__panel {
		border-top : 2px solid #EEE;
	}
}

.accordion-tabs__content {
	display            : grid;
	grid-template-rows : 0fr;
	transition         : grid-template-rows 500ms, margin-top 500ms;

	.accordion-tabs__main {
		overflow : hidden;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			overflow : visible;
		}
	}

	&.is-open {
		grid-template-rows : 1fr;
		margin-top         : 20px;

		@media screen and (min-width : $break-desktop) {
			transition : margin-top 0ms;
		}
	}

}

.accordion-tabs__head {
	overflow-x : auto;
	overflow-y : hidden;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		overflow : visible;
	}
}

.accordion-tabs__text {
	color         : $dark-grey;
	font-size     : 14px;
	line-height   : 20px;
	margin-bottom : 32px;
	max-width     : 912px;
}

.product-tabs__subtitle {
	color         : $dark-grey;
	font-size     : 14px;
	font-weight   : 700;
	line-height   : 20px;
	margin-bottom : 12px;
}

.accordion-tabs__img {
	a {
		@media screen and (min-width : $break-desktop) {
			pointer-events : none;
		}
	}
}




