.error-page {
	padding : 156px 0 196px 0;

	@media screen and (min-width : $break-table) {
		padding : 295px 0 317px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 225px 0;
	}
}

.error-page__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;
	margin         : 0 auto;
	max-width      : 300px;
	text-align     : center;

	@media screen and (min-width : $break-table) {
		max-width : 430px;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : row;
		max-width      : 970px;
		text-align     : start;
	}
}

.error-page__main {
	color       : $light-grey;
	font-size   : 132px;
	font-weight : 700;
	line-height : 132px;

	@media screen and (min-width : $break-table) {
		font-size   : 148px;
		line-height : 148px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 250px;
		line-height : 250px;
	}
}

.error-page__right {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-left : 50px;
	}
}

.error-page__title {
	color         : $black;
	font-size     : 22px;
	font-weight   : 700;
	line-height   : 30px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 40px;
		line-height : 46px;
	}
}

.error-page__text {
	color         : $dark-grey;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
