.process {
	padding : 32px 0 64px 0;

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0 96px 0;
	}
}

.process__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	.slider-nav {
		display : none;
		@media screen and (min-width : $break-table) {
			display : flex;
		}
	}
}

.swiper--process {
	margin : 0 -6px;

	@media screen and (min-width : $break-table) {
		margin : 0 -13px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -12px;
	}

	.swiper-slide {
		width : 50%;

		@media screen and (min-width : $break-table) {
			width : 33.33333%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 25%;
		}

		img {
			border-radius : 12px;
			height        : 100%;
			object-fit    : cover;
			width         : 100%;
		}
	}

	.swiper-pagination {
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.process__img {
	margin : 0 6px;

	@media screen and (min-width : $break-table) {
		margin : 0 13x;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 12px;
	}
}
