.header {
	background-color : $white;
}

.header__container {
	border-bottom : 1px solid $light-grey;
	padding       : 47px 14px 16px 14px;

	@media screen and (min-width : $break-table) {
		padding : 47px 16px 0px 16px;
	}

	@media screen and (min-width : $break-desktop) {
		border-bottom : 1px solid $light-grey;
		padding       : 24px 16px;

	}
}

.header__wrapper {
	align-items     : center;
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-table) {
		display               : grid;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}
}

.header__wrap {
	align-items : center;
	display     : flex;

	&:after {
		background-color : rgba(153, 153, 153, 0.5);
		content          : "";
		display          : none;
		height           : 100%;
		position         : absolute;
		right            : 0;
		top              : 50%;
		transform        : translateY(-50%);
		width            : 1px;
		z-index          : 1;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}
	}

	@media screen and (min-width : $break-table) {
		border-right   : none;
		grid-column    : 1/2;
		padding-bottom : 16px;
		padding-right  : 0;
	}

	@media screen and (min-width : $break-desktop) {
		align-items    : start;
		flex-direction : column;
		flex-shrink    : 0;
		padding-bottom : 0;
		padding-right  : 24px;
		position       : relative;
	}
}

.header__course {
	align-items      : center;
	background-color : $light-grey-2;
	color            : $dark-grey;
	display          : flex;
	font-size        : 12px;
	font-style       : normal;
	font-weight      : 400;
	left             : 0;
	line-height      : 18px;
	min-height       : 32px;
	padding-left     : 16px;
	position         : absolute;
	top              : 0;
	width            : 100%;

	span {
		padding-left : 5px;
	}

	&:before {
		background-image  : url("../images/icon/currency.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : inline-block;
		height            : 16px;
		margin-right      : 6px;
		width             : 16px;

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		background-color : transparent;
		font-size        : 12px;
		line-height      : 18px;
		margin-top       : 8px;
		min-height       : auto;
		padding          : 0;
		position         : unset;
		width            : auto;
	}
}

.logo {
	align-items   : center;
	display       : flex;
	padding-right : 16px;
	position      : relative;

	&:after {
		background-color : rgba(153, 153, 153, 0.5);
		content          : "";
		height           : 32px;
		position         : absolute;
		right            : 0;
		top              : 50%;
		transform        : translateY(-50%);
		width            : 1px;
		z-index          : 1;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}

	@media screen and (min-width : $break-desktop) {
		padding-right : 0;
	}

	&__icon {
		height : 20px;
		width  : 91px;
		@media screen and (min-width : $break-table) {
			height : 30px;
			width  : 137px;
		}

		@media screen and (min-width : $break-desktop) {
			width : 142px;
		}
	}

	&__text {
		color       : $grey;
		display     : none;
		font-size   : 9px;
		font-weight : 400;
		line-height : 12px;
		margin-left : 12px;
		max-width   : 134px;
		position    : relative;
		top         : 4px;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display : block;
		}
	}
}

.nav {
	align-items     : center;
	display         : flex;
	flex-direction  : column;
	justify-content : flex-start;
	width           : 100%;

	@media screen and (min-width : $break-table) {
		display : block;
		padding : 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 24px 0 24px;
		width   : auto;
	}

	&__list {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;

		width          : 100%;

		@media screen and (min-width : $break-table) {
			align-items     : center;
			flex-direction  : row;
			justify-content : space-between;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&__item {
		border-bottom : 1px solid #EEE;
		cursor        : pointer;
		padding       : 0;
		position      : relative;
		width         : 100%;
		z-index       : 4;

		@media screen and (min-width : $break-table) {
			padding : 0;
			width   : auto;
		}

		@media screen and (min-width : $break-table) {
			border-bottom : none;
			padding       : 0;
			&:not(:first-child) {
				margin-left : 9px;
			}
		}

		button {
			@media screen and (min-width : $break-table) {
				pointer-events: none;
			}
		}

		&.open {
			background-color : $light-pink-2;
			padding-bottom   : 0;

			.nav__submenu {
				margin-top : 21px;
				opacity    : 1;
				visibility : visible;
			}

		}

		&:hover {
			.nav__submenu {
				opacity    : 1;
				visibility : visible;
			}

			.nav__link {
				color : $pink;

				svg {
					pointer-events : none;
					transform      : rotate(180deg);
					transition     : transform .3s ease;

					path {
						stroke     : $pink;
						transition : fill .3s ease;
					}
				}
			}
		}
	}

	&__link {
		align-items      : center;
		background-color : transparent;
		border           : none;
		color            : $dark-grey;
		display          : inline-flex;
		font-size        : 14px;
		font-weight      : 400;
		line-height      : 20px;
		padding          : 20px 14px;
		transition       : color .3s ease;
		width            : 100%;

		@media screen and (min-width : $break-table) {
			font-size   : 12px;
			line-height : 18px;
			padding     : 6px 6px;
		}

		svg {
			margin-left    : 4px;
			pointer-events : none;
			transition     : transform .3s ease;

			path {
				stroke     : $pink;
				transform  : rotate(0);
				transition : fill .3s ease;
			}
		}
	}

	&__submenu {
		box-sizing  : content-box;
		margin      : 0 -12px;
		max-height  : 0;
		opacity     : 0;
		overflow    : hidden;
		transition  : 0.3s ease-out;
		will-change : max-height;

		@media screen and (min-width : $break-table) {
			left        : 0;
			max-height  : none;
			opacity     : 0;
			overflow    : visible;
			padding-top : 46px;
			position    : absolute;
			top         : 0;
			transition  : visibility .3s ease, opacity .3s ease;
			visibility  : hidden;
			z-index     : 3;
		}

		@media screen and (min-width : $break-desktop) {
			padding-top : 54px;
		}

		li {
			background-color : #F6F6F6;
			min-width        : 312px;
			//padding          : 20px 20px;
			transition       : color .3s ease, background-color .3s ease;

			&:not(:last-child) {
				border-bottom : 1px solid #EEE;
			}

			@media screen and (min-width : $break-table) {
				border : none;
				//padding : 14px 24px;
			}

			& > ul {
				opacity    : 0;
				position   : absolute;
				right      : -100%;
				top        : 54px;
				visibility : hidden;

				@media screen and (min-width : $break-table) {
					display : none;
				}

				@media screen and (min-width : $break-desktop) {
					display : block;
				}

				li {
					.product-card {
						border-radius : 0;
						cursor        : unset;
						margin        : 0;
						opacity       : 0;
						position      : absolute;
						right         : -100%;
						top           : 0;
						visibility    : hidden;

						.product-card__wrapper {
							border-radius : 0;
						}
					}

					&:hover {
						.product-card {
							opacity    : 1;
							visibility : visible;
						}

						a {
							color   : $pink;
							padding : 20px 20px;;

							@media screen and (min-width : $break-table) {
								padding : 14px 24px;
							}
						}
					}
				}
			}

			&:hover {
				background-color : $light-pink-2;

				& > ul {
					opacity    : 1;
					visibility : visible;
				}

				& > a {
					color : $pink;
				}
			}

			a {
				align-items : center;
				color       : $dark-grey;
				display     : flex;
				font-size   : 14px;
				line-height : 20px;
				padding     : 20px 20px;

				@media screen and (min-width : $break-table) {
					padding : 14px 24px;
				}

				span {
					padding-left : 16px;
				}
			}
		}
	}
}

.header__group {


	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		grid-column: 5/13;
		justify-content : flex-end;
		padding-bottom  : 16px;
	}

	@media screen and (min-width : $break-desktop) {
		flex-shrink    : 0;
		padding-bottom : 0;
	}


	.b-btn {
		@media screen and (min-width : $break-table) {
			margin-left : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 32px;
		}
	}
}

.header__phone {
	color       : $dark-grey;
	display     : none;
	font-size   : 14px;
	font-weight : 700;
	line-height : 20px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		display : inline-block;
	}

	@media screen and (min-width : $break-desktop) {

	}

	@media screen and (min-width : $break-desktop) {

		&:hover {
			&:before {
				width : 100%;
			}
		}
	}

	&:before {
		background-color : $dark-grey;
		bottom           : 0;
		content          : "";
		height           : 1px;
		left             : 0;
		position         : absolute;
		transition       : width .3s ease;
		width            : 0;
		z-index          : 1;
	}
}

.header__panel {
	background-color : #FFF;
	height           : 100dvh;
	opacity          : 0;
	overflow-x       : hidden;
	overflow-y       : auto;
	position         : absolute;
	right            : 0;
	top              : 32px;
	transition       : visibility .3s ease, opacity .3s ease;
	visibility       : hidden;
	width            : 100%;
	z-index          : 100;

	&.active {
		opacity    : 1;
		visibility : visible;
	}

	@media screen and (min-width : $break-table) {
		border-bottom : none;
		border-top    : 1px solid $light-grey;
		grid-column   : 1/13;
		grid-row      : 2/2;
		height        : auto;
		overflow-x    : unset;
		overflow-y    : unset;
		padding       : 16px 16px;
		top           : 93px;

		&.active {
			opacity    : 1;
			position   : unset;
			top        : 61px;
			visibility : visible;
		}
	}

	@media screen and (min-width : $break-desktop) {
		border     : none;
		display    : block;
		opacity    : 1;
		padding    : 0;
		position   : unset;
		visibility : visible;
	}
}

.header__menu {


}

.header__top {
	border-bottom   : 1px solid #EEE;
	display         : flex;
	justify-content : space-between;
	padding         : 18px 14px;
	width           : 100%;

	@media screen and (min-width : $break-table) {
		display : none;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.logo {
		border-right : none;
	}
}

.header__mail {
	padding : 26px 14px;
	width   : 100%;

	.header__link {
		align-items     : center;
		display         : flex;
		font-size       : 16px;
		justify-content : flex-start;
		margin-left     : 0;

		&:before {
			margin-right : 16px;
		}
	}

	@media screen and (min-width : $break-table) {
		display : none;
	}
}

.header__tel {
	align-items : center;
	display     : flex;
	padding     : 26px 14px;
	width       : 100%;

	.header__link {
		align-items     : center;
		display         : flex;
		font-size       : 16px;
		justify-content : flex-start;
		margin-left     : 0;

		&:before {
			margin-right : 16px;
		}
	}

	@media screen and (min-width : $break-table) {
		display : none;
	}
}

.header__link {
	align-items : center;
	color       : $dark-grey;
	display     : none;
	font-size   : 14px;
	font-weight : 700;
	line-height : 20px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		display : flex;
	}

	@media screen and (min-width : $break-desktop) {
		&:hover {
			&:after {
				width : calc(100% - 25px);
			}
		}
	}


	&:after {
		background-color : $dark-grey;
		bottom           : 0;
		content          : "";
		height           : 1px;
		left             : 25px;
		position         : absolute;
		transition       : width .3s ease;
		width            : 0;
		z-index          : 1;
	}

	&:before {
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		display           : inline-block;
		height            : 20px;
		margin-right      : 6px;
		position          : relative;
		top               : -2px;
		width             : 20px;
	}

	&--tel {
		&:before {
			background-image : url("../images/icon/phone-small.svg");
		}
	}

	&--mail {
		&:before {
			background-image : url("../images/icon/mail-small.svg");
		}

	}

	& + & {
		margin-left : 24px;
	}
}

.header__callback {
	align-items      : center;
	background-color : $pink;
	border           : none;
	border-radius    : 4px;
	display          : flex;
	height           : 32px;
	justify-content  : center;
	padding          : 8px 8px;
	width            : 32px;

	svg {
		flex-shrink : 0;
		height      : 16px;
		width       : 16px;
	}

	@media screen and (min-width : $break-table) {
		display : none;
	}
}

.menu-close {
	background-color : transparent;
	border           : none;
	padding          : 0;
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $pink;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	margin-left      : 16px;
	padding          : 0;
	position         : relative;
	width            : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display : none;
	}

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 3px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&:after {
		left  : unset;
		right : 0;
		width : 100%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		height           : 3px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 50%;
	}

	@media screen and (min-width : $break-table) {
		&--active {
			&::before {
				top        : 50%;
				transform  : rotate(45deg);
				transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
				width      : 100%;
			}

			&::after {
				top        : 50%;
				transform  : rotate(-45deg);
				transition : transform 0.3s ease-in-out, top 0.3s ease-in-out;
				width      : 100%;
			}

			.burger__line {
				transform  : scale(0);
				transition : transform 0.3s ease-in-out;
			}
		}
	}

}
