.contact-banner {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0;
	}
}

.contact-banner__wrapper {
	align-items           : center;
	background-color      : $light-grey-2;
	background-image      : url("../images/contacts/banner/pedrollo-bg.svg");
	background-position   : top right;
	background-repeat     : no-repeat;
	background-size       : 330px 321px;
	border-radius         : 12px;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	padding               : 24px 24px;
	position              : relative;

	@media screen and (min-width : $break-table) {
		background-size       : contain;
		border-radius         : 12px;
		grid-template-columns : repeat(12, 1fr);
		padding               : 32px 0 32px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 21px 33px 21px 64px;
	}
}

.contact-banner__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact-banner__logo {
	height   : 26px;
	left     : 25px;
	position : absolute;
	top      : 25px;
	width    : 110px;

	@media screen and (min-width : $break-table) {
		left          : unset;
		margin-bottom : 32px;
		position      : unset;
		top           : unset;
	}

	@media screen and (min-width : $break-desktop) {
		height        : 40px;
		margin-bottom : 70px;
		width         : 170px;
	}
}

.contact-banner__title {
	color         : $dark-grey;
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		line-height   : 28px;
		margin-bottom : 12px;
		max-width     : 470px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact-banner__text {
	color         : $dark-grey;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact-banner__img {
	grid-column : 4 span;
	grid-row    : 1/1;
	margin      : 28px -24px 0 -24px;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		grid-row    : unset;
		margin      : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}
