.news-section {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0;
	}
}

.news-section__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.news-section__wrap {
	align-items : center;
	display     : flex;

	.slider-nav {
		display : none;

		@media screen and (min-width : $break-table) {
			display     : flex;
			margin-left : 12px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 24px;
		}
	}
}

.news-section__slider {
	margin : 0 -14px;

	@media screen and (min-width : $break-table) {
		margin : 0 -12px;
	}

	@media screen and (min-width : $break-desktop) {
		overflow : hidden;
	}
}

.swiper--news {
	overflow : visible;

	.swiper-slide {
		display : flex;
		height  : auto;
		width   : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 33.3333%;
		}
	}

	.swiper-pagination {
		@media screen and (min-width : $break-table) {
			display : none;
		}
	}
}

.news-card {
	background-color : $light-grey-2;
	border-radius    : 8px;
	display          : flex;
	flex-direction   : column;
	margin           : 0 14px;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		margin        : 0 13px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 12px;
	}
}

.news-card__img {
	height : 185px;

	@media screen and (min-width : $break-table) {
		height : 192px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 220px;
	}


	img {
		border-top-left-radius  : 8px;
		border-top-right-radius : 8px;
		height                  : 100%;
		object-fit              : cover;
		width                   : 100%;

		@media screen and (min-width : $break-table) {
			border-top-left-radius  : 12px;
			border-top-right-radius : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.news-card__wrapper {
	display        : flex;
	flex-direction : column;
	height         : 100%;
	padding        : 12px 12px;

	@media screen and (min-width : $break-table) {
		padding : 16px 12px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 32px 24px 24px 24px;
	}

	.b-btn--outline-pink {
		font-size   : 10px;
		line-height : 14px;
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 14px;
			line-height : 20px;
		}
	}
}

.news-card__title {
	color         : $black;
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 28px;
		margin-bottom : 16px;
	}
}

.news-card__text {
	-webkit-box-orient : vertical;
	color              : $dark-grey;
	display            : -webkit-box;
	font-size          : 14px;
	font-weight        : 400;
	-webkit-line-clamp : 4;
	line-height        : 20px;
	margin-bottom      : 24px;
	overflow           : hidden;

	@media screen and (min-width : $break-table) {
		-webkit-line-clamp : 3;
	}

	@media screen and (min-width : $break-desktop) {
		font-size          : 16px;
		-webkit-line-clamp : 2;
		line-height        : 24px;
		margin-bottom      : 24px;
	}
}

.news-card__wrap {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-top      : auto;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.news-card__date {
	align-items : center;
	color       : $dark-grey;
	display     : flex;
	font-size   : 9px;
	font-weight : 400;
	line-height : 12px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 20px;
	}

	&:before {
		background-image : url("../images/icon/calendar.svg");
		background-size  : cover;
		content          : "";
		display          : inline-block;
		height           : 16px;
		margin-right     : 6px;
		width            : 16px;
	}
}
