.representation {
	padding-bottom : 32px;

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 48px;
	}
}

.representation__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.representation__title {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.representation__main {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
	}
}

.representation__subtitle {
	color         : $dark-grey;
	font-size     : 18px;
	font-weight   : 700;
	line-height   : 24px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 28px;
	}
}

.representation__text {
	font-size   : 14px;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.representation__img {
	border-radius : 8px;
	grid-column   : 4 span;
	grid-row      : 2/2;

	@media screen and (min-width : $break-table) {
		border-radius : 12px;
		grid-column   : 12 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 6 span;
		grid-row    : unset;
	}


	img {
		border-radius : 8px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;

		@media screen and (min-width : $break-table) {
			border-radius : 12px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.representation__bottom {
	display               : grid;
	grid-column           : 4 span;
	grid-gap              : 48px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column           : 12 span;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 104px;
	}
}

.representation__item {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3 span;
	}

	h4 {
		font-size     : 14px;
		font-weight   : 700;
		line-height   : 20px;
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 20px;
		}
	}

	.representation__text {
		font-size   : 14px;
		line-height : 20px;
	}
}

.representation__icon {
	height        : 32px;
	margin-bottom : 12px;
	width         : 32px;

	@media screen and (min-width : $break-table) {
		height        : 40px;
		margin-bottom : 24px;
		width         : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}
