@import './_vars.scss';
@import './animations/fade.scss';
@import './themes/light.scss';

$color : #333;

[data-#{$namespace-prefix}-root] {
	max-width : calc(100vw - 10px);
}

.#{$namespace-prefix}-box {
	background-color    : $pink;
	border-radius       : 4px;
	color               : white;
	font-size           : 14px;
	line-height         : 20px;
	outline             : 0;
	padding             : 10px 10px;
	position            : relative;
	transition-property : transform, visibility, opacity;
	white-space         : initial;

	&[data-placement^='top'] > .#{$namespace-prefix}-arrow {
		bottom : 0;

		&::before {
			border-top-color : initial;
			border-width     : 8px 8px 0;
			bottom           : -7px;
			left             : 0;
			transform-origin : center top;
		}
	}

	&[data-placement^='bottom'] > .#{$namespace-prefix}-arrow {
		top : 0;

		&::before {
			border-bottom-color : initial;
			border-width        : 0 8px 8px;
			left                : 0;
			top                 : -7px;
			transform-origin    : center bottom;
		}
	}

	&[data-placement^='left'] > .#{$namespace-prefix}-arrow {
		right : 0;

		&::before {
			border-left-color : initial;
			border-width      : 8px 0 8px 8px;
			right             : -7px;
			transform-origin  : center left;
		}
	}

	&[data-placement^='right'] > .#{$namespace-prefix}-arrow {
		left : 0;

		&::before {
			border-right-color : initial;
			border-width       : 8px 8px 8px 0;
			left               : -7px;
			transform-origin   : center right;
		}
	}

	&[data-inertia][data-state='visible'] {
		transition-timing-function : cubic-bezier(0.54, 1.5, 0.38, 1.11);
	}
}

.#{$namespace-prefix}-arrow {
	& {
		color  : $color;
		height : $arrow-size;
		width  : $arrow-size;
	}

	&::before {
		border-color : transparent;
		border-style : solid;
		content      : '';
		position     : absolute;
	}
}

.#{$namespace-prefix}-content {
	padding  : 5px 9px;
	position : relative;
	z-index  : 1;
}
